.portafolios {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
}

.portafolios__item {
  width: 100%;
}
.more {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}
.button-extra {
  display: flex;
  justify-content: center;
}
.card-meta {
  text-align: right;
}

.showPortafolio-info-titulo {
  margin-bottom: 20px;
}

.titulo-portafolio {
  font-size: 40px;
  color: aliceblue;
}

.portafolios__item {
  > .ui.card {
    height: 420px;
  }

  > .ui.card > .content > .description {
    display: -webkit-box;
    -webkit-line-clamp: 4; /* Número de líneas que deseas mostrar */
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
@media (max-width: 1600px) {
  .portafolios {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 1200px) {
  .portafolios {
    grid-template-columns: 1fr;
  }
}
