$widthLeftMenu: 400px;

.portafolio {
  //background-image: url("../../../assets/bghome.gif");
  display: flex;
  background: radial-gradient(#0844a4, #1e70f4);
  min-height: 100vh;
  background-position: center;
  background-size: cover;
  @media (max-width: 1200px) {
    flex-direction: column;
  }
}

.portafolio > div {
  flex: 1;
}

.portafolio > div:first-child {
  margin-left: 100px;
  margin-top: 50px;
  position: fixed;
  transition: top 0.3s ease-in-out;

  @media (max-width: 1200px) {
    position: relative;
    margin-left: auto;
    margin-right: auto;
  }
}

.showPortafolio-container {
  margin-left: $widthLeftMenu;
  margin-top: 50px;
  margin-bottom: 50px;

  @media (max-width: 1200px) {
    margin-left: auto;
    margin-right: auto;
  }
}
