.portafolio-form__miniature {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 1rem;

  input {
    display: none;
  }

  .image {
    width: 200px !important;
    height: 100px;
    object-fit: cover;
    border: 2px dashed gray;
    padding: 2px;
    cursor: pointer;
  }
}

.portafolio-form__images {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1rem;
  margin-bottom: 1rem;

  .portafolio-form__imagen {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 2px dashed gray;
    padding: 0rem;
    cursor: pointer;

    input {
      display: none;
    }

    .image {
      width: 100%;
      height: 100px;
      object-fit: cover;
    }
  }
}
