.error-captcha {
  color: red;
  font-weight: bold;
}

.contactoform-form {
  border: 1px solid #ccc;
  padding: 80px;
  border-radius: 10px;
  width: 70%;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;

  > .ui.form .field > label {
    color: white;
  }

  > .ui.form .field {
    margin-bottom: 30px;
  }
  > .ui.form .field.error label {
    color: red;
  }

  @media (max-width: 1200px) {
    width: 80%;
    margin-left: 25px;
    padding: 40px;
  }
}

.contactoform-title {
  color: aliceblue;

  &__titulo {
    font-size: 40px;
  }

  @media (max-width: 1200px) {
    margin-left: 10px;
  }
}
.recaptcha {
  margin-bottom: 30px;
  @media (max-width: 1200px) {
    transform: scale(0.85);
    transform-origin: 0;
  }
}
