.top-bar {
  display: flex;

  align-items: center;
  justify-content: space-between;
  padding: 20px;
  background-color: #0844a4;

  &__left {
    display: flex;
    align-items: center;
  }

  &__right {
    display: flex;
    justify-content: center;
    flex-grow: 1;
  }

  .logo {
    height: 60px;
    margin-right: 10px;
  }

  .menu {
    border: none !important;

    .item {
      font-size: 18px;
      padding: 12px 16px;
    }

    // Agregar espacio entre los Menu.Item
    .item + .item {
      margin-left: 16px;
    }
  }
  .burgerMenu {
    display: none;
    background-color: transparent;
    border: none;
  }
  .top {
    display: none;
    position: absolute;
    z-index: 9999;
    right: 80px;
    background-color: #0844a4;
  }
}

.burgerMenu.active .burgerLine:first-child {
  width: 35px;
  transform: rotate(45deg) translate(0, 10px);
}

.burgerMenu.active .burgerLine:nth-child(2) {
  opacity: 0;
}

.burgerMenu.active .burgerLine:last-child {
  width: 35px;
  transform: rotate(-45deg) translate(0, -10px);
}

@media (max-width: 1200px) {
  .burgerMenu {
    display: block !important;
  }

  .burgerMenu {
    display: block;
    position: relative;
    align-self: center;
    margin-left: auto;
    height: 50px;
    width: 50px;
  }
  .burgerLine {
    display: block;
    width: 25px;
    height: 3px;
    background-color: white;
    margin-bottom: 5px;
    transition: transform 0.3s, opacity 0.3s;
  }

  .top-bar-menu {
    display: none;
  }

  .redes-topbar {
    display: none;
  }
}
