$widthLeftMenu: 400px;

.curriculum {
  //background-image: url("../../../assets/bghome.gif");
  display: flex;
  background: radial-gradient(#0844a4, #1e70f4);
  min-height: 100vh;
  background-position: center;
  background-size: cover;
  @media (max-width: 1200px) {
    flex-direction: column;
  }
}

.curriculum > div {
  flex: 1;
}

.curriculum > div:first-child {
  margin-left: 100px;
  margin-top: 50px;
  position: fixed;
  transition: top 0.3s ease-in-out;

  @media (max-width: 1200px) {
    position: relative;
    margin-left: auto;
    margin-right: auto;
  }
}

.showCurriculum-container {
  margin-left: $widthLeftMenu;
  margin-top: 50px;

  margin-right: 100px;

  @media (max-width: 1200px) {
    margin-left: auto;
    margin-right: auto;
  }
}
