.sidebar {
  //display: flex;
  //flex-direction: column;
  //align-items: center;
  //text-align: center;

  img {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    border: 2px solid white;
    margin: 5px; /* Ajusta el valor según la separación deseada */
    padding: 5px; /* Ajusta el valor según el tamaño deseado del borde */
  }

  h3 {
    margin-top: 10px;
    color: white;
    font-size: 20px;
  }

  .social-media {
    margin-top: 20px;

    > div {
      display: flex;
      flex-direction: row;
      align-items: center;
      margin-bottom: 20px;
      font-size: 20px;

      .custom-button {
        margin-bottom: 10px;
        margin-right: 10px;
        background-color: transparent !important;
        border: none !important;
        padding: 0 !important;
        margin: 0 10px;
      }

      a {
        display: flex;
        align-items: center;
        justify-content: center;
        font-family: "Source Sans Pro";
        color: white;
        height: 100%;
        text-decoration: none;
      }

      a,
      a:hover,
      a:active,
      a:visited {
        font-family: "Source Sans Pro";
        color: white;
      }
    }
  }
}
