.ui.menu.admin-menu {
  > .item {
    display: flex;
    color: white;
    padding: 15px 20px !important;
    margin: 0 !important;
    border-radius: 0 !important;

    &:hover,
    &.active,
    &.active:hover {
      color: white;
      background-color: #0280b3;
    }

    > i {
      margin: 0 !important;
      margin-right: 10px !important;
    }
  }
}
