$widthLeftMenu: 200px;

.admin-layout {
  display: flex;
  min-height: 100vh;
  max-height: 100vh;
  background-color: white;

  &__left {
    background-image: url("../../assets/bg4.jpg");
    width: $widthLeftMenu;
    display: flex;
    flex-direction: column;
    align-items: center;

    .logo {
      height: 60px;
      width: auto;
      background-image: url("../../assets/bg4.jpg");
    }
  }

  &__right {
    width: calc(100% - $widthLeftMenu);

    &-header {
      background-image: url("../../assets/bg4.jpg");
      height: 60px;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      padding: 0 10px;
    }

    &-content {
      position: relative;
      margin: 20px;
    }
  }
}
