.Footer {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.952);
  color: white;
  min-height: 20vh;
  font-size: 20pt;

  .custom-button {
    background-color: transparent !important;
    border: none !important;
    padding: 0 !important;
    margin: 0 10px;
  }

  a {
    margin-right: 20px;
  }

  a,
  a:hover,
  a:active,
  a:visited {
    font-family: "Source Sans Pro";
    color: white;
  }

  .Container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
  }
  .derechos {
    font-family: "Source Sans Pro";
    margin-top: 50px;
    margin-bottom: 10px;
    font-size: 15pt;
  }
  .dereachos2 {
    font-family: "Source Sans Pro";
    font-size: 15pt;
  }
}
