.contenido {
  display: flex;
  flex-direction: row;
  margin-top: 100px;

  @media (max-width: 1200px) {
    flex-direction: column;
  }
}

.carousel-container {
  flex: 1;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: aliceblue;

  @media (max-width: 1200px) {
    margin-bottom: 50px;
  }
}
.redirect-button {
  margin-top: 20px;
  @media (max-width: 1200px) {
    margin-top: 0px;
  }
}

.grid-container {
  flex: 1;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: aliceblue;

  @media (max-width: 1200px) {
    margin-top: 50px;
    margin-bottom: 20px;
  }
}

.carousel-image {
  width: 100%;
  height: 300px;

  @media (max-width: 1200px) {
    height: 150px;
  }
}
.grid-image {
  width: auto;
  height: 300px;
  border-radius: 10px;

  @media (max-width: 1200px) {
    height: 150px;
    margin-bottom: 40px;
  }
}

.prev {
  display: none;
}

.next {
  display: none;
}

.dots {
  display: none;
}

.fade-in {
  animation: fadeIn 0.2s ease-in-out;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
