.auth {
  background-image: url("../../../assets/bg3.gif");
  min-height: 100vh;
  background-position: center;
  background-size: cover;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding-top: 50px;

  .logo {
    width: 500px;
  }
}

@media (max-width: 1200px) {
  .auth {
    background-image: url("../../../assets/bg3.gif");
    .logo {
      width: 300px;
    }
  }
}
